<template>
  <div class="page" id="siteIntro">
    <mt-header class="bar-nav" title="平台公告">
      <mt-button slot="left" icon="back" v-back-link></mt-button>
    </mt-header>
    <div class="page-infinite-wrapper"  ref="wrapper" :style="{ height: wrapperHeight +'px' }">
      <div class="list-box margin-t-10" v-infinite-scroll="loadMore" infinite-scroll-disabled="loading" infinite-scroll-immediate-check="false" infinite-scroll-distance="50">

        <mt-cell v-for="item in list" :title="item.title" :label="item.remark" :to="'/mine/siteIntro/detail/'+item.uuid" is-link></mt-cell>

      </div>
    </div>
  </div>
</template>

<script>
  import * as ajaxUrl from '../../ajax.config'
  export default {
    data(){
      return {
        list: []
      }
    },
    created(){ this.projectList() },
    mounted(){
      this.wrapperHeight = document.documentElement.clientHeight - this.$refs.wrapper.getBoundingClientRect().top;
    },
    methods: {
      projectList( page = 1){
        this.noData = false;
        this.$indicator.open({spinnerType: 'fading-circle'})
        let getParams = {
          sectionCode: 'notice',
          'page.page': page
        }
        this.$http.get(ajaxUrl.getArticleList, {params: getParams}).then((res) => {
          this.$indicator.close()
          this.list = this.list.concat(res.data.resData.list)
          if(this.list.length == 0){this.noData = true;}
        })
      },
      loadMore() {
        this.loading = true;
        this.page += 1
        setTimeout(() => {
          this.projectList(this.page)
          this.loading = false;
        }, 500);
      }
    }
  }
</script>



